import Link from "next/link";
import Image from "next/image";

interface ProductCardProps {
  id: string;
  name: string;
  images: string[];
  price: number;
  discountPrice?: number;
  category: string;
  subcategory: string;
}

export default function ProductCard({
  id,
  name,
  images,
  price,
  discountPrice,
  category,
  subcategory,
}: ProductCardProps) {
  return (
    <Link
      href={`/shop/${category}/${subcategory}/${id}`}
      className="block max-w-7xl mx-auto bg-white p-4 w-[60%] rounded-lg border shadow-sm hover:shadow-md transition"
    >
      {/* Product Image */}
      <div className="relative h-48 mb-4 group">
        <Image
          src={
            images[0]
              ? `http://localhost:5000${images[0]}`
              : "/placeholder.svg"
          }
          alt={name}
          layout="fill"
          objectFit="cover"
          className="rounded-md transition-opacity group-hover:opacity-80"
        />
      </div>

      {/* Product Info */}
      <div className="space-y-2 text-center">
        <h3 className="font-medium text-lg">{name}</h3>
        <div className="text-lg font-semibold">
          ${price.toFixed(2)}
          {discountPrice && (
            <span className="ml-2 text-base line-through text-gray-500">
              ${discountPrice.toFixed(2)}
            </span>
          )}
        </div>
      </div>
    </Link>
  );
}
